*{
    margin:0px;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
    text-decoration: none;
}
*:focus{
    outline: none;
}
body {
  background: #005792;
  
}

.main--form{
    display:flex;
    flex-direction: row;
    justify-content: center;
    
}
.main--login--form,.main--signup--form{
    /* display: flex; */
    background:white;
    position: relative;
    top:70px;
    width:600px;
    height: 440px;
    /* border:2px solid black; */
    padding:50px;
    border-radius:20px;
}
.main--sign,.main--signup{
    position: relative;
    font-size: 35px;
    color:#005792;
    font-weight: 100;
}
.main--user,.main--first{
    position: relative;
    top:40px;
    color:#005792;
    font-weight: 800;
    font-size: 20px;
    letter-spacing: 1px;
}

.main--input--user,.main--input--firstname,.main--input--lastname,
.main--signup--user,.main--signup--email,
.main--signup--password,.main--signup--cpassword{
    position: relative;
    top:10px;
    width:100%;
    height: 40px;
    font-size: 20px;
    /* border:2px solid #005792; */
    border-style: none;
    border-bottom:2px solid black;
    padding:5px 20px;
    /* border-radius:10px; */
    
}
.main--input--user:focus,.main--input--firstname:focus,
.main--input--lastname:focus,.main--signup--user:focus,
.main--signup--email:focus,.main--signup--password:focus,
.main--signup--cpassword:focus
{
    border:4px solid #005792;
    border-radius:30px;
    
    /* outline: none; */
}
.main--pass{
    position: relative;
    top:60px;
    color:#005792;
    font-weight: 800;
    font-size: 20px;
    letter-spacing: 1px;
}
.main--input--pass{
    position: relative;
    top:10px;
    width:100%;
    height: 40px;
    font-size: 20px;
    /* border:2px solid #005792; */
    border-style: none;
    border-bottom:2px solid black;
    padding:5px 20px;
    /* border-radius:10px; */
}
.main--input--pass:focus{
    border:4px solid #005792;
    border-radius:30px;
    
    /* outline: none; */
}
.main--input--submit,.main--signup--submit{
    position: relative;
    top:80px;
    left:50px;
    height: 40px;
    width:80%;
    font-size: 20px;
    font-weight: bolder;
    background: white;
    border-radius:10px;
    border-style: none;
    border:3px solid #005792;
    color: #005792;
}
.main--input--submit:hover,.main--signup--submit:hover{
    background:#005792;
    color: white;
    cursor: pointer;
    border:3px solid #005792;
}
.main--create-account{
    position: relative;
    top:70px;
    text-align: center;
    padding-top: 30px;
}
.main--signup--form{
    height:100%;
    padding-bottom:250px;
    margin-bottom: 100px;
}
.main--input--firstname{
    width:40%;
}
.main--input--lastname{
    position: relative;
    left:100px;
    width:40%;
}
.main--last--label{
    position: relative;
    left:195px;
}
.main--signup--username,.main--signup--emailaddress,.main--signup--pass,.main--signup--cpass{
    position: relative;
    top:70px;
    color:#005792;
    font-weight: 800;
    font-size: 20px;
    letter-spacing: 1px;
}
/* .main--signup--email{
    margin-bottom: 30px;
} */
.main--signup--emailaddress{
    top:100px;
    
}
.main--signup--pass{
    top:130px;
}
.main--signup--cpass{
    top:160px;
}
.main--signup--submit{
    top:200px;
}
